kbd,
.keycap {
    display: inline;
    display: inline-block;
    min-width: 1em;
    padding: 0.3em 0.5em;
    font: normal 0.85em/1 'Lucida Grande', Lucida, Arial, sans-serif;
    text-align: center;
    text-decoration: none;
    -moz-border-radius: 0.3em;
    -webkit-border-radius: 0.3em;
    border-radius: 0.3em;
    border: none;
    cursor: default;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}

kbd[title],
.keycap[title] {
    cursor: help;
}

/* Dark style for display on light background. This is the default style. */
kbd.dark,
.dark kbd,
.keycap.dark,
.dark .keycap {
    background: rgb(80, 80, 80);
    background: -moz-linear-gradient(top, rgb(60, 60, 60), rgb(80, 80, 80));
    background: -webkit-gradient(
        linear,
        left top,
        left bottom,
        from(rgb(60, 60, 60)),
        to(rgb(80, 80, 80))
    );
    color: rgb(250, 250, 250);
    text-shadow: -1px -1px 0 rgb(70, 70, 70);
    box-shadow: inset 0 0 1px rgb(150, 150, 150),
        inset 0 -0.05em 0.4em rgb(80, 80, 80), 0 0.1em 0 rgb(30, 30, 30),
        0 0.1em 0.1em rgba(0, 0, 0, 0.3);
}

/* Light style for display on dark background. */
kbd,
kbd.light,
.light kbd,
.keycap,
.keycap.light,
.light .keycap {
    background: rgb(250, 250, 250);
    color: rgb(50, 50, 50);
    text-shadow: 0 0 2px rgb(255, 255, 255);
    box-shadow: inset 0 0 1px rgb(255, 255, 255),
        inset 0 0 0.4em rgb(200, 200, 200), 0 0.1em 0 rgb(130, 130, 130),
        0 0.11em 0 rgba(0, 0, 0, 0.4), 0 0.1em 0.11em rgba(0, 0, 0, 0.9);
}
